.main-footer2{
  position: static;
  padding: 0;
  @media screen and (min-width: 993px){
    grid-template-columns: 50% 50%;
  }
  .main-link{
    background-color: $primary;
    color: #fff;
    &:hover{
      background-color: rgba($primary,.35);
    }
    @media screen and (min-width: 993px){
      padding-left: 3.125em;
    }
    &__arrow{
      background-color: rgba($main1,.2);
    }
  }
}

.about2{
  @media screen and (min-width: 993px){
    padding: 5em 0 6.75em;
    .about__groups{
      margin-top: 5em;
    }
  }
}

.steps{
  padding: 8em 0 9em;
  &__title{
    text-align: center;
  }
  &__grid{
    margin-top: 3.75em;
    display: grid;
    grid-template-columns: 50% 50%;
  }
}

.step-card{
  padding: 2.5em;
  &:hover{
    color: $primary;
    .text{
      color: $primary;
    }
    .step-card__title:after{
      background-color: $primary;
    }
  }
  @media screen and (min-width: 993px){
    &:nth-child(4n - 3),
    &:nth-child(4n){
      background: $gray;
    }
  }
  &__title{
    display: flex;
    &:after{
      content: '';
      flex-shrink: 0;
      width: 1.5em;
      height: .125em;
      background: $black;
      transform: translateY(1em);
      margin-left: .5625em;
    }
    .text24{
      line-height: 108%;
      max-width: 11.5em;
    }
  }
  &__info{
    margin-top: 4.375em;
    @include grid100gap(1.25em);
  }
  .text{
    h4{
      max-width: 17em;
    }
    h4,
    p{
      font-size: 1em;
    }
    ol{
      @include grid100gap(.4375em);
      counter-reset: list;
      padding-left: .5em;
      li{
        font-size: 1em;
        line-height: 162%;
        counter-increment: list;
        display: flex;
        &:before {
          content: counter(list) '.';
          flex-shrink: 0;
          min-width: 1.5em;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .steps{
    padding: 1em 0;
    &__grid{
      margin-top: 1.5em;
    }
  }

  .step-card{
    padding: 1em;
    &__info{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .steps{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
