.directions {
  padding: 0 0 4.6875em;
  &__subtitle {
    margin: 0.9375em 0;
    span {
      font-size: 1em;
      line-height: 120%;
      letter-spacing: 0.02em;
      color: #0F1D40;
    }
  }
  &__nav-title {
    font-size: 1.25em;
    line-height: 140%;
  }
  ul {
    li {
      font-size: 1.25em;
      line-height: 140%;
    }
  }
}

.laboratories {
  &__items {
    display: flex;
  }
}

.laboratories-card {
  width: 17.5em;
  &:not(:first-child) {
    margin-left: 1.375em;
  }
  &__img {
    margin-bottom: 0.625em;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 0.75em;
    line-height: 140%;
    opacity: 0.75;
  }
}


@media screen and (max-width: 992px) {
  .directions {
    padding-bottom: 1em;
    ul {
      li {
        font-size: .875em;
      }
    }
    &__nav-title {
      font-size: 0.875em;
    }
  }

  .laboratories-card {
    width: calc(100% / 3);
    &:not(:first-child) {
      margin-left: 0.9375em;
    }
  }
}

@media screen and (max-width: 576px) {
  .laboratories {
    &__items {
      display: block;
    }
  }

  .laboratories-card {
    width: 100%;
    &:not(:first-child) {
      margin: 0.9375em 0 0 0;
    }
  }
}
