.certificates{
  padding: 4.5em 0;
  overflow: hidden;
  &__title{
    text-align: center;
  }
  &__slider{
    margin-top: 3.75em;
  }
  @media screen and (min-width: 993px) {
    .swiper{
      &-wrapper {
        display: grid;
        grid-template-columns: repeat(4,calc((100% / 4) - (1.25em * 3 / 4)));
        gap: 1.375em 1.25em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}


.certificate-card{
  display: block;
  background-color: rgba($gray,.5);
  padding: 1.8125em 2.5em;
  &__img{
    @include imgCover(145%);
  }
}


@media screen and (max-width: 992px){
  .certificates{
    padding: 1em 0;
    &__title{
      font-size: 1.5em;
    }
    &__slider{
      margin-top: 1em;
    }
    .swiper{
      &-slide{
        width: 12em;
        padding-right: 1em;
      }
    }
  }

  .certificate-card{
    padding: 1em;
    background-color: $gray;
  }
}
