.graduate-page{
  padding-bottom: 5.625em;
  &__top{
    @include grid100gap(1em);
    color: $main1;
  }
}

.graduate-table{
  margin-top: 3.75em;
  background: rgba($main1,.01);
  table{
    width: 100%;
    border-collapse: collapse;
  }
  tbody{
    tr{
      transition: all .3s;
      &:hover{
        background-color: rgba($main1,.5);
        color: #fff;
      }
    }
  }
  th{
    font-weight: 400;
    padding-top: 3em;
    padding-bottom: 2.375em;
    text-align: left;
    span{
      font-size: 1.125em;
      line-height: 130%;
    }
  }
  th,
  td{
    vertical-align: unset;
    leading-trim: both;
    text-edge: cap;
    padding-right: 2em;
    &:first-child{
      padding-left: 2.5em;
      text-align: center;
    }
    &:nth-child(2){
      width: 11em;
    }
    &:nth-child(3){
      width: 13em;
    }
    &:nth-child(4){
      width: 8em;
    }
    &:last-child{
      padding-right: 3.5em;
    }
  }
  td{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    span{
      font-size: .875em;
      line-height: 130%;
    }
    .small-text{
      font-size: .75em;
    }
  }
}

.site-map{
  margin-top: 3.75em;
  display: flex;
  justify-content: space-between;
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2.8125em;
  }
  &__link{
    @include lastMb(2.3125em);
    span{
      font-size: 1em;
      line-height: 131%;
    }
  }
}


@media screen and (max-width: 992px){
  .graduate-page{
    padding-bottom: 1em;
  }

  .graduate-table{
    margin-top: 2em;
    @include fullScroll;
    table{
      min-width: 71em;
    }
  }

  .site-map{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 2em;
    margin-top: 1.5em;
    &__links{
      margin-top: 1.5em;
    }
    &__link {
      @include lastMb(1em);
    }
  }
}


@media screen and (max-width: 576px){
  .graduate-table{
    width: calc(100% + 2em);
    padding: 0 1em;
    margin-left: -1em;
    th,td{
      padding: .5em 1em !important;
      max-width: calc(100vw - 2em);
    }
  }

  .site-map{
    grid-template-columns: 100%;
  }
}
